/* Sidebar container */
.sidebar {
  height: 100vh;
  background-color: #1a1b21;
  color: #999999;
  font-weight: bold;
  box-shadow: 2px 0px 15px rgba(0, 0, 0, 0.2);
  /* display: flex;
  flex-direction: column; */
}

/* Sidebar Header - Fixed */
.sidebar-header {
  text-align: center;
  padding: 10px;
  position: sticky;
  top: 0;
  background-color: #1a1b21;
  z-index: 10;
}

.sidebar-header .logo {
  max-width: 60px;
  height: auto;
  border-radius: 50%;
  margin-bottom: 8px;
}

.sidebar-header p {
  font-size: 0.75rem;
  font-weight: 600;
  margin: 4px 0;
  letter-spacing: 0.5px;
  color: #f2f4f7;
}

/* Sidebar Menu - Scrollable */
.sidebar-menu {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  flex: 1;
  scrollbar-width: thin;
  scrollbar-color: #444 #1E1E2D;
}

/* Thanh cun cho trnh duyt WebKit (Chrome, Edge, Safari) */
.sidebar-menu::-webkit-scrollbar {
  width: 6px;
}

.sidebar-menu::-webkit-scrollbar-track {
  background: #1E1E2D;
}

.sidebar-menu::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 5px;
}

.sidebar-menu::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.sidebar-menu .menu-section {
  font-size: 0.7rem;
  color: #767373;
  text-transform: none;
  border-bottom: 1px solid #444;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.arrow-icon {
  font-size: 20px;
  margin-right: 8px;
}

.sidebar-menu .menu-section .arrow-icon {
  transition: transform 0.3s ease;
}

.sidebar-menu .menu-section .arrow-icon.open {
  transform: rotate(90deg);
}

.sidebar-menu li {
  transition: background-color 0.3s ease;
}

.sidebar-menu li a {
  margin-top: 5px;
  text-decoration: none;
  color: #a1a3a1;
  font-weight: bold;
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  padding: 8px 12px;
  border-radius: 12px;
  transition: all 0.3s ease;
  margin-left: 8px;
}

.sidebar-menu li a .icon {
  margin-right: 8px;
}

/* Hover effect for links */
.sidebar-menu li a:hover {
  background-color: #2c5282;
  color: white;
}

/* Active menu item */
.sidebar-menu li a.active {
  background-color: #2c5282;
  color: white;
  border-radius: 12px;
}

.sidebar-menu li:has(a.active) {
  background-color: transparent;
}

.section-items {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.section-items.open {
  max-height: 500px;
}

/* Sidebar Footer - Fixed */
.sidebar-footer {
  width: 100%;
  padding: 8px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 5pt;
  background-color: #232429; /* Darker than sidebar background */
  position: sticky;
  bottom: 0;
  z-index: 10;
  border-top: 1px solid #2d2e35; /* Subtle border to separate from content */
  box-shadow: 0 -4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

/* Update button styles to match new footer background */
.sidebar-footer button {
  background: #1a1b21; /* Slightly darker than original background */
  border: 2px solid #2d2e35;
  color: #a1a3a1;
  font-size: 1.2em;
  cursor: pointer;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

/* Nt Logout */
.sidebar-footer .logout-button:hover {
  background: red;
  color: white;
  transform: translateY(-15px);
}

/* Nt Profile */
.sidebar-footer .profile-button:hover {
  background: yellow;
  color: white;
  transform: translateY(-15px);
}

/* Nt Help */
.sidebar-footer .help-button:hover {
  background: blue;
  color: white;
  transform: translateY(-15px);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.icon {
  margin-right: 8px;
}

.modal {
  background: #fff;
  padding: 16px;
  border-radius: 6px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.modal p {
  color: #2d2c2c;
  font-size: 0.875rem;
}

.modal-buttons {
  margin-top: 12px;
}

.confirm-button {
  background-color: red;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  font-size: 0.875rem;
}

.cancel-button {
  background-color: grey;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.875rem;
}

.confirm-button:hover {
  background-color: darkred;
}

.cancel-button:hover {
  background-color: darkgrey;
}

