.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
  gap: 4px;
}

/* Sizes */
.small {
  height: 35px;
  font-size: 10px;
}
.notify{
  font-weight: bold;
  padding:5px 10px;
  font-size: 10px;
}

.medium {
  height: 40px;
  padding: 0 16px;
  font-size: 16px;
}

.large {
  height: 48px;
  padding: 0 24px;
  font-size: 18px;
}

/* Variants */
.filled.primary {
  background-color: #1890ff;
  color: white;
}

.filled.success {
  background-color: #52c41a;
  color: white;
}

.filled.warning {
  background-color: #faad14;
  color: white;
}

.filled.danger {
  background-color: #ff4d4f;
  color: white;
}

.filled.info {
  background-color: #13c2c2;
  color: white;
}

/* Outlined variant */
.outlined {
  background-color: transparent;
  border: 1px solid;
}

.outlined.primary {
  border-color: #1890ff;
  color: #1890ff;
}

.outlined.success {
  border-color: #52c41a;
  color: #52c41a;
}

.outlined.warning {
  border-color: #faad14;
  color: #faad14;
}

.outlined.danger {
  border-color: #ff4d4f;
  color: #ff4d4f;
}

.outlined.info {
  border-color: #13c2c2;
  color: #13c2c2;
}

/* Text variant */
.text {
  background-color: transparent;
}

.text.primary {
  color: #1890ff;
}

.text.success {
  color: #52c41a;
}

.text.warning {
  color: #faad14;
}

.text.danger {
  color: #ff4d4f;
}

.text.info {
  color: #13c2c2;
}

/* Hover states */
.filled:hover:not(:disabled) {
  opacity: 0.9;
}

.outlined:hover:not(:disabled),
.text:hover:not(:disabled) {
  background-color: rgba(0, 0, 0, 0.04);
}

/* Disabled state */
.button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Full width */
.fullWidth {
  width: 100%;
}

/* Icon */
.icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Label */
.label {
  white-space: nowrap;
}

/* Purpose-specific styles */
.add {
  background-color: #00cb77 !important;
  color: #fff !important;
}
.edit {
  background-color: #0e87f6 !important;
  color: #fff !important;
}

.delete {
  background-color: #f5575a !important;
  color: #fff !important;
}

.load {
  background-color: #faad14 !important;
  color: #fff !important;
}

.download {
  background-color: #13c2c2 !important;
  color: #fff !important;
}

.upload {
  background-color: #722ed1 !important;
  color: #fff !important;
}

.filter {
  background-color: #2db7f5  !important;
  color: #fff !important;
}
