/* TargetSelectionComponent.module.css */

/* nh dng cho cc section (gii tnh, khoa, kha, ngnh, lp, target) */
.section {
    margin-bottom: 20px;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* nh dng cho cc tiu  h3 */
.title {
    font-size: 18px;
    margin-bottom: 10px;
    color: #007bff;
    border-bottom: 2px solid #007bff;
    padding-bottom: 5px;
}

/* nh dng cho cc label v checkbox */
.label {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 16px;
    color: #555;
}

/* nh dng cho container cha cc checkbox theo chiu ngang */
.checkbox-container {
    display: flex; /* S dng Flexbox */
    flex-wrap: wrap; /* Cho php xung dng nu khng  ch */
    gap: 15px; /* Khong cch gia cc checkbox */
}

/* nh dng cho checkbox */
.checkbox {
    margin-right: 10px;
    width: 18px;
    height: 18px;
    cursor: pointer;
    accent-color: #007bff; /* Mu khi checkbox c chn */
}

/* nh dng cho phn hin th target */
.targetPre {
    background-color: #2c3e50;
    color: #ecf0f1;
    padding: 15px;
    border-radius: 8px;
    font-size: 14px;
    white-space: pre-wrap;
    word-wrap: break-word;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* nh dng cho cc checkbox "Tt c" */
.allLabel {
    font-weight: bold;
    color: #007bff;
}

/* Hover effect cho label */
.label:hover {
    color: #007bff;
    transition: color 0.3s ease;
}