/* Thm vo file CreateSurvey.module.css */
.evaluate-table {
    width: 100%;
    overflow-x: auto;
    margin-top: 10px;
  }
  
  .evaluate-table table {
    width: 100%;
    border-collapse: collapse;
    min-width: 600px;
  }
  
  .evaluate-table th, .evaluate-table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
  }
  
  .evaluate-table th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  
  .evaluate-input-container {
    position: relative;
    width: 100%;
  }
  
  .evaluate-input {
    width: 80px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    text-align: center;
  }
  
  .input-error {
    border-color: #ff4d4f;
  }
  
  .error-tooltip {
    position: absolute;
    bottom: calc(100% + 5px);
    left: 50%;
    transform: translateX(-50%);
    background-color: #ff4d4f;
    color: white;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 12px;
    white-space: nowrap;
    z-index: 10;
  }
  
  .error-tooltip::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #ff4d4f transparent transparent transparent;
  }