/* Header.module.css */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
  background: #fff;
  border-bottom: 1px solid #eee;
  border-radius: 16px 16px 0 0;
  margin-bottom: 10px;
}

.headerLeft {
  display: flex;
  align-items: center;
  gap: 24px;
}

.headerRight {
  display: flex;
  align-items: center;
  gap: 16px;
}

.header h1 {
  font-size: 24px;
  margin: 0;
}

.tabs {
  display: flex;
  gap: 8px;
  align-items: center;
}

.tab {
  font-weight: bold;
  height: 40px;
  padding: 0 20px;
  border: none;
  background-color: #f5f5f5;
  cursor: pointer;
  font-size: 16px;
  border-radius: 16px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tab:hover {
  background-color: #e0e0e0;
}

.tab.active {
  background-color: #d9eafd;
  color: #578fca;
  font-weight: bold;
}

.tab.active::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #4caf50;
}

.searchContainer {
  margin-left: auto;
  display: flex;
  align-items: center;
  height: 40px;
}

.searchButton {
  height: 40px;
  padding: 0 16px;
  background-color: #f5f5f5;
  border: none;
  border-radius: 16px 0 0 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 3px;
}

.searchInput {
  height: 40px;
  padding: 0 16px;
  border: none;
  background-color: #f5f5f5;
  border-radius: 0 16px 16px 0;
  font-size: 14px;
  width: 250px;
  outline: none;
}

.searchInput:focus {
  background-color: #e8e8e8;
}

.searchButton:hover {
  background-color: #e0e0e0;
}

.endComponent {
  margin-left: 16px;
}
