/* Danh sch sinh vin */
.studentList {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Table styles */
.table-wrapper {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  height: calc(100vh - 350px); /* Gim chiu cao  khng che sidebar */
  min-height: 65vh; /* m bo c chiu cao ti thiu */
  display: flex;
  flex-direction: column;
  width: 100%; /* m bo bng khng vt qu container */
  max-width: calc(100vw - 300px); /*  li khng gian cho sidebar */
}

/* Table header styles */
.table-header {
  background-color: #d9eafd;
  position: sticky;
  top: 0;
  width: 100%;
}

.table-body-container {
  flex: 1;
  overflow-y: auto;
  overflow-x: auto;
  width: 100%;
}

.table {
  width: 100%;
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  min-width: 1200px; /* m bo bng khng b v khi c nhiu ct */
}

/* Cu hnh chung cho tt c cc  */
.table th,
.table td {
  padding: 12px 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.table th {
  background-color: white;
  padding: 12px 8px;
  font-weight: 700;
  color: #2c5282;
  text-transform: uppercase;
  font-size: 13px;
  border-bottom: 2px solid #bee3f8;
}

/* nh ngha  rng theo t l % cho tng ct */
.col-stt {
  width: 5% !important;
}
.col-id {
  width: 5% !important;
}

.col-name {
  width: 15% !important;
}

.col-date {
  width: 8% !important;
}

.col-gender {
  width: 7% !important;
}

.col-class {
  width: 8% !important;
}

.col-department {
  width: 15% !important;
}

.col-major {
  width: 15% !important;
}

.col-course {
  width: 6% !important;
}

.col-email {
  width: 15% !important;
}

.col-actions {
  width: 10% !important;
}
.actions {
  display: flex;
  gap: 4px;
  justify-content: flex-start;
  flex-wrap: wrap;
}

/* Style cho phn action buttons */
.action-buttons {
  display: flex;
  gap: 2px;
  justify-content: flex-start;
}

.action-buttons button {
  padding: 4px 8px;
  font-size: 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap;
  background-color: #007bff;
  color: white;
}

