.filterContainer {
  display: flex;
  align-items: center;
  gap: 16px;
}
.filterToolbar {
  background-color: white;
  padding: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e8e8e8;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  margin-bottom: 10px;
}

.filterSection {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.filterItem {
  position: relative;
  min-width: 180px;
}

.filterLabel {
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
  color: #333333;
  font-weight: 500;
}

.dropdown {
  position: relative;
  width: 100%;
}

.selectedOption {
  border: 1px solid #d9d9d9;
  border-radius: 16px;
  padding: 8px 12px;
  background-color: #fff;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.dropdownArrow {
  font-size: 10px;
  margin-left: 8px;
}

.dropdownMenu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  max-height: 300px;
  overflow-y: auto;
  display: none;
}

.dropdown:hover .dropdownMenu {
  display: block;
}

.dropdownItem {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
}

.dropdownItem:hover {
  background-color: #f5f5f5;
}

.checkbox {
  margin-right: 8px;
}

.selectAllContainer {
  padding: 8px 12px;
  border-bottom: 1px solid #e8e8e8;
}

.selectAllLabel {
  display: flex;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
}

.selectAllCheckbox {
  margin-right: 8px;
}

.actionButtons {
  display: flex;
  gap: 8px;
}

.actionButton {
  padding: 8px 16px;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
}

.actionButton:hover {
  opacity: 0.85;
}

.buttonIcon {
  margin-right: 8px;
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .filterToolbar {
    flex-direction: column;
    align-items: stretch;
  }

  .filterSection {
    margin-bottom: 16px;
  }

  .actionButtons {
    justify-content: flex-end;
  }
}
