/* App container */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Ty chnh thanh cun cho ton b trang */
html, body {
  height: 100%; /* m bo chiu cao y  */
  margin: 0;
  overflow: hidden; /* n thanh cun mc nh trn body */
}

.app-container {
  display: flex;
  height: 100%;
  overflow: hidden; /* m bo khng c thanh cun mc nh */
}

.sidebar {
  flex: 15;
  background-color: #2c3e50;
  padding: 10px 10px 10px 20px; ;
  color: white;
  min-height: 100vh; /* m bo chiu cao ti thiu */
  overflow-y: auto; /* Thm thanh cun ring cho sidebar */
  scrollbar-width: thin; /* Thu hp thanh cun cho Firefox */
  scrollbar-color: #ccc #2c3e50; /* Mu thanh cun cho Firefox */
}

.sidebar::-webkit-scrollbar {
  width: 8px; /*  rng thanh cun dc */
}

.sidebar::-webkit-scrollbar-track {
  background: #2c3e50; /* Mu nn thanh cun */
}

.sidebar::-webkit-scrollbar-thumb {
  background: #444; /* Mu thanh ko */
  border-radius: 4px; /* Bo trn thanh ko */
}

.sidebar::-webkit-scrollbar-thumb:hover {
  background: #555; /* Mu khi hover */
}

.main-content {
  flex: 85;
  background-color: #f2f4f7;
  padding: 20px;
  overflow-y: auto; /* Cho php cun ni dung chnh */
  scrollbar-width: thin; /* Thanh cun nh trn Firefox */
  scrollbar-color: #ccc #ffffff; /* Mu thanh cun */
}

.main-content::-webkit-scrollbar {
  width: 6px;
}

.main-content::-webkit-scrollbar-track {
  background: #fafafa; /* Mu nn thanh cun */
  border-radius: 16px;
}

.main-content::-webkit-scrollbar-thumb {
  background: #888; /* Mu thanh ko */
  border-radius: 16px;
}

.main-content::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.login-container input {
  margin: 10px;
  padding: 10px;
  width: 200px;
}

.login-container button {
  padding: 10px 20px;
  background-color: #2c3e50;
  color: white;
  border: none;
  cursor: pointer;
}

.login-container button:hover {
  background-color: #34495e;
}