.notificationItem {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    text-align: left; /* Cn l tri */
}

.notificationItem:last-child {
    border-bottom: none; /* Khng c ng vin cho item cui cng */
}

.notificationItem h3 {
    margin: 0;
    font-size: 18px;
    text-align: left; /* Cn l tri cho tiu  */
}

.notificationItem p {
    margin: 5px 0;
    color: #555;
}

.notificationItem .notificationTime {
    font-size: 12px;
    color: #999;
}

.notificationNote {
    font-weight: 500;
    font-size: 14px; /* Kch thc ch cho ghi ch */
    color: #333; /* Mu sc cho ghi ch */
    margin-bottom: 5px; /* Khong cch di ghi ch */
}

.statusContainer{
    display: inline-flex;
    gap: 8px;
    align-items: center;
    justify-content: center;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.actions {
  display: flex;
  gap: 8px;
}