.form-container {
    display: flex;
    justify-content: center; /* Canh gia theo chiu ngang */
    align-items: center; /* Canh gia theo chiu dc */
    height: 100vh;
    background-color: #f4f4f4;
  }
  
  .wrapper {
    width: 100%;
    max-width: 400px; /* Gii hn chiu rng form */
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .wrapper h2 {
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    color: #565353;
  }

  .wrapper p {
    text-align: center;
    font-size: 16px;
    color: #b3acac;
    margin-bottom: 50px;
  }
  
  .logo {
    display: block;
    margin: 0 auto 20px; /* Canh gia logo v cch phn di 20px */
    max-width: 100px; /* t kch thc ti a ca logo */
    height: auto; /* Gi t l ca logo */
  }
  
  .form-group {
    margin-bottom: 15px;
    text-align: left;
  }
  
  .form-group label {
    display: block;
    font-size: 14px;
    color: #333;
    margin-bottom: 5px;
  }
  
  .email-form {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 8px; /* Lm trn cc gc */
    box-sizing: border-box;
    background-color: rgba(255, 255, 255, 0.8); /* Mu nn m */
    transition: all 0.3s ease; /* Hiu ng chuyn i */
  }

  .email-form:focus {
    border-color: #007bff; /* i mu vin khi focus */
    background-color: rgba(255, 255, 255, 1); /* Nn trong sut khi focus */
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.5); /* Hiu ng bng m */
    outline: none; /* B vin mc nh */
  }
  
  /* Thm hiu ng cho  nhp khi hover */
  .email-form:hover {
    box-shadow: 0 0 8px rgba(0, 123, 255, 0.3); /* Bng m khi hover */
  }
  
  
  .submitButton {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submitButton:hover {
    background-color: #0056b3;
  }
  