.createTab {
  width: 100%;
  max-width: 1500px;
  margin: 0 auto;
  background: #fff;
  height: 80vh;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.1) transparent !important;
}

/* Custom scrollbar cho Chrome, Safari v Edge */
.createTab::-webkit-scrollbar {
  width: 6px;
}

.createTab::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 20px;
  margin: 4px 0; /* Thm margin  to khong cch cho border gc */
}

.createTab::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  border: transparent; /* B border 2px */
  background-clip: content-box; /* i t padding-box thnh content-box */
}

.createTab::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.15);
}

.formRow {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
}

.label {
  display: block;
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
}

.input,
.select,
.textarea {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.3s;
  box-sizing: border-box;
}

.input:focus,
.select:focus,
.textarea:focus {
  outline: none;
  border-color: #1890ff;
}

.textarea {
  resize: vertical;
  min-height: 120px;
}

.radioGroup {
  display: flex;
  gap: 24px;
  margin-top: 8px;
}

.radioLabel {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.radioLabel input[type="radio"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.radioLabel span {
  font-size: 14px;
  color: #333;
}

.actions {
  margin-top: 24px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.previewBtn,
.submitBtn,
.closeBtn {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.previewBtn {
  background: #6c757d;
  color: white;
}

.submitBtn {
  background: #1890ff;
  color: white;
  border: none;
  padding: 12px 32px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submitBtn:hover {
  background: #096dd9;
}

.submitBtn:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.closeBtn {
  background: #dc3545;
  color: white;
}