.notificationList {
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    max-height: 70vh; /* Chim 90% chiu cao ca viewport */
    overflow-y: auto; /* Cho php cun dc */
    scrollbar-width: thin; /* i vi Firefox */
    scrollbar-color: #ccc transparent; /* i vi Firefox */
    margin-bottom: 10px;
}

/* n thanh cun cho Chrome, Safari v Edge */
.notificationListTab::-webkit-scrollbar {
    width: 0; /* n thanh cun */
    background: #ccc; /* Nn trong sut */
}



