/* Table Container */
.table-wrapper {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  height: calc(100vh - 350px);
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  width: 100%;
}

/* Table header styles */
.table-header {
  background-color: #d9eafd;
  position: sticky;
  top: 0;
  width: 100%;
}

.table-body-container {
  flex: 1;
  overflow-y: auto;
  overflow-x: auto;
  width: 100%;
}

.survey-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  min-width: 1200px;
}

/* Common cell configuration */
.survey-table th,
.survey-table td {
  padding: 12px 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.survey-table th {
  background-color: white;
  padding: 12px 8px;
  font-weight: 700;
  color: #2c5282;
  text-transform: uppercase;
  font-size: 13px;
  border-bottom: 2px solid #bee3f8;
}

/* Column widths */
.col-title {
  width: 25% !important;
}

.col-target {
  width: 10% !important;
}

.col-published {
  width: 15% !important;
}

.col-expired {
  width: 15% !important;
}

.col-status {
  width: 15% !important;
}

.col-actions {
  width: 20% !important;
}

/* Status styles */
.active {
  color: #28a745;
  font-weight: 500;
  padding: 4px 8px;
  border-radius: 4px;
  background-color: rgba(40, 167, 69, 0.1);
}

.inactive {
  color: #dc3545;
  font-weight: 500;
  padding: 4px 8px;
  border-radius: 4px;
  background-color: rgba(220, 53, 69, 0.1);
}

/* Action buttons */
.actions {
  display: flex;
  gap: 4px;
  justify-content: flex-start;
  flex-wrap: wrap;
}

/* Tooltip */
.survey-table td[title]:hover::after {
  content: attr(title);
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  white-space: normal;
  max-width: 300px;
  z-index: 1000;
  margin-top: -30px;
  margin-left: 10px;
}

/* Custom scrollbar */
.table-body-container::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.table-body-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.table-body-container::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

.table-body-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}